body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  background-color: #7690b7;
  border-color: #687e9e;
  padding: 4px;
}

button:hover {
  background-color: #6b87b1;
  border-color: #687e9e;
}



.editor-tools-panel {
  display: flex;
}
.play-panel-container {
    padding-bottom: 20px;
}

.play-panel-container .border-info {
    border: 1px solid #dee2e6;
    border-radius: 5px;
}

.play-panel-container .card-header {
    font-size: 18px;
    padding: 5px;
    border-bottom: 1px solid #dee2e6;
}

.play-panel-container .card-body {
    padding: 5px;
}

.play-panel-container .card .btn {
    margin: 0 5px;
    height: auto;
}

.button-icon {
    display: block;
    width: 32px;
    height: 32px;
    background-repeat: no-repeat;
    background-size: contain;
}

.button-play {
    background-image: url(data:image/png;base64,);
}

.button-stop {
    background-image: url(data:image/png;base64,);
}

.tempo-panel, .time-signature-panel {
    margin-bottom: 0;
}
.sequencer-container {
    padding: 0 20px;
}

.sequencer-roll {
    width: auto;
}

.sequencer-roll th {
    width: 30px;
    height: 30px;
    border-bottom: none;
}

.sequencer-roll td, .sequencer-roll th {
    width: 30px;
    height: 30px;
    text-align: center;
    padding: 2px;
    border: 1px solid #dee2e6;
}

.sequencer-roll .bar-header-item {
    position: relative;
}

.sequencer-roll  .remove-icon {
    background-image: url(data:image/png;base64,);
    display: inline-block;
    width: 12px;
    height: 12px;
    background-repeat: no-repeat;
    background-size: contain;
    position: absolute;
    right: 4px;
    top: 4px;
}

.sequencer-roll .bar-header-item:hover .remove-icon:hover {
    cursor: pointer;
}

.sequencer-roll .bar-header-item:hover {
    cursor: default; 
}

.sequencer-roll .active-note:hover {
    background-color: #fff1f2;
    cursor: default;
}

.sequencer-roll .inactive-note:hover {
    background-color: #e5fcf5;
    cursor: default;
}

.sequencer-roll .beat-header {
    font-size: 12px;
    font-weight: normal;
}

.sequencer-roll .add-bar-item .add-icon {
    background-image: url(data:image/png;base64,);
    display: inline-block;
    width: 16px;
    height: 16px;
    background-repeat: no-repeat;
    background-size: contain;
}

.sequencer-roll .add-bar-item:hover .add-icon:hover {
    cursor: pointer;
}
.app-header {
  font-size: 24px;
}

.app-header .column {
  text-align: center;
}
