.play-panel-container {
    padding-bottom: 20px;
}

.play-panel-container .border-info {
    border: 1px solid #dee2e6;
    border-radius: 5px;
}

.play-panel-container .card-header {
    font-size: 18px;
    padding: 5px;
    border-bottom: 1px solid #dee2e6;
}

.play-panel-container .card-body {
    padding: 5px;
}

.play-panel-container .card .btn {
    margin: 0 5px;
    height: auto;
}

.button-icon {
    display: block;
    width: 32px;
    height: 32px;
    background-repeat: no-repeat;
    background-size: contain;
}

.button-play {
    background-image: url(data:image/png;base64,);
}

.button-stop {
    background-image: url(data:image/png;base64,);
}

.tempo-panel, .time-signature-panel {
    margin-bottom: 0;
}